.booklist {
  display: flex;
  flex-direction: column;
  justify-content: center;
  h4 {
    margin: 0;
    font-size: 14px;
    font-weight: 500;
    .statu {
      font-size: 11px;
      background: #b93321;
      color: #fff;
      display: inline-block;
      padding: 2px;
      border-radius: 2px;
      transform: scale(.6);
    }
  }
  .book-des {
    font-size: 10px;
    color: #aaa;
  }
  .book-info {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    margin-top: 10px;
    color: #444;
    font-weight: 300;
    .book-type {
      display: inline-block;
      padding: 0px 3px;
      border: 1px solid #b93321;
      border-radius: 2px;
      color: #b93321;
      transform: scale(.7);
    }
  }
}
.empty-list {
  text-align: center;
  margin-top: 100px;
  font-size: 12px;
  color: #999
}



