.bookinfo {
  padding: 15px;
  background: #fff;
  border-top: 1px solid #eee
}
.book-title-info {
  display: flex;
  flex-direction: column;
  .book-author {
    color: #b93321
  }
  .book-detail {
    font-size: 12px;
    margin-top: 10px;
    color: #999
  }
  .update-time {
    font-size: 12px;
    color: #999;
    margin-top: 5px
  }
}
.book-action {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  .button-link {
    text-decoration: none;
    display: inline-block;
  }
  .add-list {
    padding: 10px 15px;
    border: 1px solid #b93321;
    margin-right: 15px;
    color: #b93321;
    border-radius: 2px;
  }
  .view-content {
    padding: 10px 15px;
    background: #b93321;
    margin-right: 15px;
    color: #fff;
    border-radius: 2px;
  }
  .start-read {
    padding: 10px 15px;
    background: #15b59f;
    color: #fff;
    border-radius: 2px;
  }
}

.all-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: #d9d9d9;
  .close-content {
    position: fixed;;
    bottom: 0;
    width: 100%;
    height: 50px;
    background: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    .close-btn {
      width: 30px;
      height: 30px;
      background: #b93321;
      border-radius: 100px;
      color: #fff;
      text-align: center;
      line-height: 1.8rem
    }
  }
  .single-content {
    color: #777;
    padding: 10px 10px;
    border-bottom: 1px dashed #777;
    list-style:circle;
    display: block;
  }
}



