.search {
  padding: 10px;
  input {
    padding: 6px 15px;
    border: 1px solid #aaa;
    width: 70%;
    border-radius: 3px;
    &:focus {
      outline: none;
      border: 1px solid #b93321
    }
  }
  button {
    -webkit-appearance: none;
    padding: 6px 15px;
    border: none;
    background: #b93321;
    color: #fff;
    border-radius: 3px;
  }
}



